/**
 * @jest-environment jsdom
 */
import { renderBridgeHook } from './test-utils/index';
import { useBridgeLayer } from '../src/hooks/useBridgeLayer';

// Mock business layer dependencies for performance tests
jest.mock('../src/business-layer', () => ({
  useTryonMutation: jest.fn(() => ({
    mutate: jest.fn(),
    mutateAsync: jest.fn(),
    data: undefined,
    error: null,
    isError: false,
    isSuccess: false,
    isLoading: false,
    reset: jest.fn(),
  })),
  useImageProcessing: jest.fn(() => ({
    processImage: jest.fn(),
    processBasic: jest.fn(),
    isProcessing: false,
    error: null,
  })),
  useImageValidation: jest.fn(() => ({
    data: { isValid: true, errors: [] },
    error: null,
    isLoading: false,
    isError: false,
  })),
  useImageMetadata: jest.fn(() => ({
    data: { dimensions: { width: 1024, height: 768 }, format: 'image/jpeg', size: 102400 },
    error: null,
    isLoading: false,
    isError: false,
  })),
  useImageThumbnail: jest.fn(() => ({
    data: 'data:image/jpeg;base64,mock-thumbnail',
    error: null,
    isLoading: false,
    isError: false,
  })),
  processImageForTryon: jest.fn(),
  resizeImageTo1024x1536: jest.fn(),
}));

// Mock utility dependencies
jest.mock('../src/utils/image', () => ({
  fileToBase64: jest.fn().mockResolvedValue('data:image/jpeg;base64,mock-data'),
  compressBase64: jest.fn().mockReturnValue('data:image/jpeg;base64,compressed-mock-data'),
  CompressionFailedError: class extends Error { constructor(msg: string) { super(msg); } }
}));

describe('Debug Performance Bridge Layer', () => {
  it('should return non-null useBridgeLayer result', () => {
    const { result } = renderBridgeHook(() => useBridgeLayer());

    console.log('Performance test result:', result.current);
    console.log('Result type:', typeof result.current);
    console.log('Is null?', result.current === null);
    console.log('Is undefined?', result.current === undefined);

    if (result.current) {
      console.log('Has state?', !!result.current.state);
      console.log('Has actions?', !!result.current.actions);
      console.log('Has advanced?', !!result.current.advanced);
    }

    expect(result.current).not.toBeNull();
    expect(result.current).not.toBeUndefined();
  });
});